import React, { Component } from "react";
import { connect } from "react-redux";


class TopPeople extends Component {
  handleTop(){
    switch (this.props.Email) {
      case "":
        return "data:image/jpeg;base64,";
                
      default:
        return this.props.Pic;
    }
  }
  render() {
    
    return (
      <div className="names">
        <span>TOP PEOPLE</span>
        <img src={this.handleTop()} />
        <span>
          Name : {this.props.UserName}
          <br /> Email: {this.props.email}
        </span>
        <p style={{ marginTop: "30px" }}>CONNECTIONS</p>
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    Pic: state.Pic,
    UserName: state.UserName,
    Email: state.Email
  };
};
export default connect(mapStateToProps)(TopPeople);
